	import topBar from '../../components/TopBar.vue'
	import mInput from '../../components/m-input.vue'
	import ButtonItems from "../../components/ButtonItems.vue"
    export default {
		data(){
			return{
				cardsInfo:{},
				cardNumber:'',
				amount:this.$route.query.amount,
				fullName: ''
			}
		},
		components: {
			topBar,
			mInput,
			ButtonItems
		},
		onLoad() {
			this.getCardsInfo()	
		},
        methods: {
			getCardsInfo(){
				this.request({
					url: 'consumer/my/bank-cards',
				}).then(res => {
					this.cardsInfo = res.data.result
					let num = res.data.result.cardNumber
					let name = res.data.result.fullName
					this.fullName = name.replace(name.charAt(0), '*')
					this.cardNumber = num.substr(num.length-4)
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '信息获取失败，请稍后重试',
					});
				})
			},
			submitHandle(){
				this.request({
					url: 'consumer/my/withdraw-records',
					params:{
						amount: this.amount,
						callbackUrl:`${this.utils.getBaseUrl()}/#/pages/common/success`
					}
				}).then(res => {
					console.log(res.data.result)
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '数据提交失败，请稍后重试',
					});
				})
			}
        }
    }
